/* 2. Header */
.header {
    padding: 37px 72px 28px 60px;
    background: transparent;
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    margin: auto;
    z-index: 20;
    transition: $transition;
    /* 2.1. Header - Fixed */
    &.fixed {
        background: $white;
        padding: 30px 68px 30px 60px;
        .header-logo {
            top: -10px;
            &-main, &-small {
                color: $black; }
            &-main {
                font-size: 25px;
                line-height: 30px; }
            &-small {
                margin-top: -2px; } }
        .nav-link {
            color: $black;
            &:hover {
                color: $accent; } }
        .header-social__link {
            svg {
                path {
                    fill: $black; } }
            &:hover {
                svg {
                    path {
                        fill: $accent; } } } }
        .header-phone__link {
            color: $black;
            &:hover {
                color: $accent; } } }
    &-cnt {
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative; }
    &-inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-right: 130px;
        padding-left: 5px; }
    /* 2.2. Header - Logo */
    &-logo {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: -12px;
        left: 0;
        margin: auto;
        &-main {
            font-family: $sfpd-semibold;
            font-size: 32px;
            line-height: 38px;
            color: $white;
            transition: $transition; }
        &-small {
            font-family: $sfpt-regular;
            font-size: 13px;
            line-height: 16px;
            color: $white;
            transition: $transition;
            margin-top: -5px; } }
    &-nav {
        margin-right: 40px;
        &-list {
            display: flex;
            align-items: center; } }
    /* 2.3. Header - Social */
    &-social {
        display: flex;
        align-items: center;
        margin-right: 27px;
        &__item {
            margin-right: 33px;
            &:last-child {
                margin-right: 0; } }
        &__link {
            display: flex;
            align-items: center;
            justify-content: center;
            svg {
                path {
                    transition: $transition; } }
            &:hover {
                svg {
                    path {
                        fill: $accent; } } } } }
    &-phone {
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        margin: auto;
        &__link {
            font-family: $sfpd-semibold;
            font-size: 17px;
            line-height: 25px;
            color: $white;
            text-decoration: none;
            transition: $transition;
            &:hover {
                color: $accent; } }
        .header-social {
            display: none; } }
    /* 2.4. Header - Navbar */
    .nav {
        &-item {
            margin-right: 45px;
            position: relative;
            &:after {
                content: '';
                height: 35px;
                width: 100%;
                background: transparent;
                position: absolute;
                left: 0;
                right: 0;
                margin: auto;
                bottom: -35px; }
            &:last-child {
                margin-right: 0; }
            &.active {
                .nav-link {
                    color: $accent; } } }
        &-link {
            font-family: $sfpt-medium;
            font-size: 14px;
            line-height: 19px;
            text-transform: uppercase;
            color: $white;
            text-decoration: none;
            transition: $transition;
            &:hover {
                color: $accent; } } }
    &--black {
        .header-logo {
            &-main, &-small {
                color: $black; } }
        .nav-link {
            color: $black;
            &:hover {
                color: $accent; } }
        .header-social__link {
            svg {
                path {
                    fill: $black; } }
            &:hover {
                svg {
                    path {
                        fill: $accent; } } } }
        .header-phone__link {
            color: $black;
            &:hover {
                color: $accent; } }
        .header-burger-line {
            background: $black; } }
    &-lang, &-contacts {
        display: none; } }
/* 2.5. Header - Burger */
.header-burger {
    width: 30px;
    height: 30px;
    cursor: pointer;
    display: none;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 100;
    &-inner {
        width: 30px;
        height: 30px;
        align-items: center;
        justify-content: space-between;
        flex-direction: column; }
    &-line {
        display: block;
        background: $white;
        width: 30px;
        height: 3px;
        transition: $transition;
        margin: 5px 0; }
    &.clicked {
        .header-burger-line {
            background: $accent;
            &:nth-child(2) {
                opacity: 0;
                transform: translateX(10px); }
            &:nth-child(1) {
                transform: rotate(45deg) translateY(7px) translateX(5px); }
            &:nth-child(3) {
                transform: rotate(-45deg) translateY(-7px) translateX(5px); } } } }

/* 2.6. Header - Dropdown */
.dropdown {
    position: relative;
    &:hover {
        .dropdown-menu {
            visibility: visible;
            opacity: 1; } }
    &-menu {
        position: absolute;
        margin-top: 35px;
        background: $white;
        padding: 30px;
        opacity: 0;
        transition: $transition;
        visibility: hidden;
        margin-left: -30px;
        &__inner {
            display: flex;
            align-items: flex-start;
            .dropdown-list {
                margin-right: 35px;
                &:last-child {
                    margin-right: 0; } } } }
    &-list {
        &__item {
            margin-bottom: 20px;
            &:last-child {
                margin-bottom: 0; }
            &.active {
                .dropdown-list__link {
                    color: $accent; } } }
        &__link {
            font-family: $sfpt-medium;
            font-size: 14px;
            line-height: 19px;
            text-transform: uppercase;
            color: $black;
            transition: $transition;
            text-decoration: none;
            white-space: nowrap;
            &:hover {
                color: $accent; } } } }
