/* 5. Main */
.main {
    flex-grow: 1; }
/* 5.1 Main - Hero */
.hero {
    min-height: 100vh;
    background-size: cover;
    position: relative;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    overflow: hidden;
    .container-fluid {
        position: relative;
        z-index: 1; }
    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        width: 100%;
        height: 100%;
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)); }
    &-cnt {
        position: relative;
        height: 100%;
        z-index: 1;
        display: flex;
        flex-direction: column;
        padding: 200px 61px 88px 0; }
    &-descr {
        font-family: $sfpt-regular;
        font-size: 15px;
        line-height: 21px;
        color: $white;
        max-width: 395px;
        width: 100%;
        padding-top: 10px; }
    &-title {
        max-width: 535px;
        width: 100%;
        margin-bottom: 205px; }
    &-items {
        display: flex;
        align-items: flex-start;
        justify-content: space-between; }
    &-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 93px;
        height: 93px;
        border-radius: 50%;
        border: 2px solid $white;
        transition: $transition;
        i {
            transition: $transition;
            transition-delay: .15s; }
        &:hover {
            transform: scale(1.1);
            background: $accent;
            border-color: $accent;
            i {
                transform: scale(1.3); } } }
    &--interior {
        align-items: stretch;
        &:after {
            content: none; }
        .swiper-pagination.swiper-pagination-bullets {
            bottom: 36px;
            left: 52px;
            right: inherit;
            text-align: left; }
        .swiper-pagination-bullets .swiper-pagination-bullet {
            width: 8px;
            height: 8px;
            background: $white;
            margin: 0 9px;
            opacity: 1;
            transition: $transition;
            &.swiper-pagination-bullet-active {
                background: $accent; } }
        .swiper-button-next, .swiper-button-prev {
            width: 23px;
            height: 43px;
            &:after {
                content: none; }
            svg {
                line {
                    transition: $transition;
                    stroke: $white; } }
            &:hover {
                svg {
                    line {
                        stroke: $accent; } } } }
        .swiper-button-prev {
            left: 65px; }
        .swiper-button-next {
            left: 120px;
            right: inherit; }
        .swiper-button-next {
            transform: rotate(180deg); } }
    &-slider {
        position: relative;
        z-index: 1; }
    &-slide {
        background-size: cover;
        background-position: center;
        height: 100%;
        display: flex;
        align-items: center;
        position: relative;
        &:after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            margin: auto;
            width: 100%;
            height: 100%; }
        &-1 {
            &:after {
                background: linear-gradient(0deg, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1)); } }
        &-2 {
            &:after {
                background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)); } }
        &-3 {
            &:after {
                background: linear-gradient(0deg, rgba(0, 0, 0, 0.23), rgba(0, 0, 0, 0.23)); } }
        &__cnt {
            padding-top: 110px;
            position: relative;
            z-index: 2; }
        &__title {
            font-size: 90px;
            line-height: 80px;
            color: $white;
            font-family: $sfpd-semibold;
            margin-bottom: 80px; }
        &__info {
            display: flex;
            align-items: center; }
        &__btn {
            margin-right: 83px; }
        &__descr {
            font-size: 15px;
            line-height: 21px;
            color: $white;
            max-width: 330px;
            width: 100%; } }
    &-inner {
        padding-bottom: 81px;
        position: relative;
        z-index: 1;
        .hero {
            &-title {
                font-size: 75px;
                line-height: 75px;
                max-width: none;
                margin-bottom: 32px; }
            &-descr {
                margin-bottom: 204px;
                font-size: 18px;
                line-height: 25px;
                color: $gray; }
            &-btn {
                &:hover {
                    transform: scale(1);
                    i {
                        transform: scale(1) translateY(10px); } } } } }
    &-bg {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        background-position: center;
        background-size: cover; }
    &-demo {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        .hero-inner {
            padding: 200px 0; }
        .hero-descr {
            margin-bottom: 0; }
        .hero-btn {
            position: absolute;
            bottom: 80px;
            right: inherit;
            z-index: 2; } } }
