@include r(1740px) {
    .planing {
        &-step {
            &__item {
                &-img {
                    position: static;
                    text-align: right;
                    margin-top: 50px; } } } }
    .header {
        padding: 24px 40px 20px 40px;
        &.fixed {
            padding: 15px 40px 15px 40px;
            .header-burger-line {
                background: $black; } }
        &-logo {
            position: static;
            margin-left: 0; }
        &-phone {
            position: static;
            white-space: nowrap; }
        &-nav-list {
            flex-direction: column;
            align-items: flex-start;
            margin-bottom: 30px; }
        .nav-item {
            margin-right: 0;
            margin-bottom: 37px;
            &:last-child {
                margin-bottom: 0; } }
        .nav-link {
            color: $black;
            font-size: 30px;
            line-height: 36px;
            letter-spacing: 0.02em;
            text-transform: capitalize; }
        .dropdown-menu {
            position: relative;
            display: none;
            visibility: visible;
            opacity: 1;
            transform: translateY(0);
            transition: none; }
        .dropdown {
            &:hover {
                .dropdown-menu {
                    visibility: visible;
                    opacity: 1;
                    transform: translateY(0); } } } }
    .header-inner {
        position: fixed;
        z-index: 50;
        background: $white;
        width: 100%;
        max-width: 504px;
        min-height: 100%;
        right: 0;
        flex-direction: column;
        top: 0;
        bottom: 0;
        margin: auto;
        padding: 40px 65px;
        align-items: flex-start;
        opacity: 0;
        transform: translateX(50%);
        visibility: hidden;
        overflow-y: auto;
        &.anime {
            transition: $transition; }
        &.active {
            opacity: 1;
            transform: translateX(0);
            visibility: visible; } }
    .header-contacts {
        display: block;
        &__link {
            font-size: 14px;
            line-height: 150%;
            color: $black;
            margin-bottom: 20px;
            display: block;
            text-decoration: none;
            &:hover {
                color: $accent; } } }
    .header-contacts__copyright {
        display: block;
        font-size: 14px;
        line-height: 150%;
        text-align: left;
        margin: 0;
        color: $black; }
    .header-contacts .social {
        display: flex;
        justify-content: flex-start;
        position: static;
        margin-bottom: 40px; }
    .header-phone {
        margin: 0 30px;
        display: flex;
        align-items: center;
        .header-social {
            display: flex;
            margin-bottom: 0;
            margin-right: 100px;
            &__link {
                svg {
                    path {
                        fill: $white; } }
                &:hover {
                    svg {
                        path {
                            fill: $accent; } } } } } }
    .header-lang {
        display: flex;
        margin-bottom: 60px;
        &__item {
            margin-right: 20px;
            &:last-child {
                margin-right: 0; }
            &.active {
                .header-lang__link {
                    color: $accent; } } }
        &__link {
            font-family: $sfpd-regular;
            font-size: 14px;
            line-height: 140%;
            text-decoration: none;
            color: $black;
            transition: $transition;
            &:hover {
                color: $accent; } } }
    .header-burger {
        display: flex; }
    .header-social {
        margin-bottom: 20px;
        &__link {
            svg {
                path {
                    fill: $black; } }
            &:hover {
                svg {
                    path {
                        fill: $accent; } } } } }
    .dark {
        .header-inner {
            background: #242424; }
        .header-lang__link {
            color: $white;
            &:hover {
                color: $accent; } }
        .dropdown-menu {
            background: transparent; }
        .dropdown-list__link {
            color: $white;
            &:hover {
                color: $accent; } }
        .header-contacts {
            &__link {
                color: $white;
                &:hover {
                    color: $accent; } }
            &__copyright {
                color: $white; } }
        .header-burger.clicked {
            .header-burger-line {
                background: $white; } }
        .header-burger {
            .header-burger-line {
                background: $white; } } } }
