/* 3. Footer */
.footer {
    padding: 115px 60px 90px;
    border-top: 1px solid $black;
    &-items {
        display: flex;
        align-items: flex-start;
        justify-content: space-between; }
    /* 3.1. Footer - Logo */
    &-logo {
        display: flex;
        flex-direction: column;
        &-main {
            font-family: $sfpd-semibold;
            font-size: 32px;
            line-height: 38px;
            color: $black; }
        &-small {
            font-family: $sfpt-regular;
            font-size: 13px;
            line-height: 16px;
            color: $black;
            margin-top: -5px; } }
    /* 3.2. Footer - Copyright */
    &-copyright {
        font-family: $sfpd-regular;
        font-size: 14px;
        line-height: 140%; }
    /* 3.3. Footer - List */
    &-list {
        &__item {
            margin-bottom: 10px;
            &.active {
                .footer-list__link {
                    color: $accent; } } }
        &__link {
            font-family: $sfpd-regular;
            font-size: 14px;
            line-height: 140%;
            text-decoration: none;
            color: $black;
            transition: $transition;
            &:hover {
                color: $accent; } } }
    /* 3.4. Footer - Social */
    &-social {
        display: flex;
        align-items: center;
        &__item {
            margin-right: 30px;
            &:last-child {
                margin-right: 0; } }
        &__link {
            svg {
                path {
                    transition: $transition; } }
            &:hover {
                svg {
                    path {
                        fill: $accent; } } } } } }
