/* 4.Dark theme */
.dark {
    background: #1D1D1D;
    .header {
        background: #1D1D1D;
        .header-logo {
            &-main, &-small {
                color: $white; } }
        .nav-item.active {
            .nav-link {
                color: $accent; } }
        .nav-link {
            color: $white;
            &:hover {
                color: $accent; } }
        .header-social__link {
            svg {
                path {
                    fill: $white; } }
            &:hover {
                svg {
                    path {
                        fill: $accent; } } } }
        .header-phone__link {
            color: $white;
            &:hover {
                color: $accent; } }
        &.fixed {
            background: #1D1D1D;
            padding: 30px 68px 30px 60px;
            .header-logo {
                top: -10px;
                &-main, &-small {
                    color: $white; }
                &-main {
                    font-size: 25px;
                    line-height: 30px; }
                &-small {
                    margin-top: -2px; } }
            .nav-item.active {
                .nav-link {
                    color: $accent; } }
            .nav-link {
                color: $white;
                &:hover {
                    color: $accent; } }
            .header-social__link {
                svg {
                    path {
                        fill: $white; } }
                &:hover {
                    svg {
                        path {
                            fill: $accent; } } } }
            .header-phone__link {
                color: $white;
                &:hover {
                    color: $accent; } } } }
    .dropdown-menu {
        background: #242424; }
    .dropdown-list {
        &__link {
            color: $white;
            &:hover {
                color: $accent; } } }
    .section {
        &--gray {
            background: #242424; } }
    .title {
        color: $white;
        &-line {
            background: $white; } }
    .inner {
        &-title {
            color: $white;
            &-wrap {
                &:after {
                    background: rgba(255, 255, 255, .3); } } }
        &-btn {
            color: $white; }
        &-nav {
            &__link {
                color: $white;
                &:hover {
                    color: $accent; } }
            &__item {
                &.active {
                    .inner-nav__link {
                        color: $accent; } } } } }
    .form-control {
        border-color: $white;
        background: #242424;
        color: $white;
        &::placeholder {
            color: rgba(255, 255, 255, .8); } }
    .create {
        &-item {
            &__descr {
                color: $white; }
            &__title {
                color: $white; } } }
    .service {
        &-item {
            border-color: $white;
            &__title, &__descr {
                color: $white; }
            &__cnt {
                border-color: $white; }
            &__arrow {
                border-color: $white;
                &-icon svg path {
                   fill: $white; } }
            &__toggle.active .service-item__arrow-icon svg path {
                fill: #1E1E1E; } } }
    .project {
        &-title {
            color: $white; }
        &-slide {
            &__hover {
                background: #242424;
                border: 1px solid #FFFFFF; }
            &__title {
                color: $white;
                border-color: $white; }
            &__descr {
                color: $white; } } }
    .swiper-button-prev, .swiper-button-next {
        svg line {
            stroke: $white; } }
    .testimonials {
        &-slide {
            &__title {
                color: $white; }
            &__header {
                &:after {
                    background: $white; } }
            &__descr {
                color: $white; } } }
    .news {
        &-title {
            color: $white; }
        &-list {
            &__item {
                border-color: $white; }
            &__link {
                color: $white;
                &:hover, &.active {
                    color: $accent; } }
            &__arrow {
                border-color: $white; } } }
    .room {
        &-item {
            &__cnt {
                background: #242424; }
            &__title {
                color: $white;
                border-color: $white; }
            &__descr {
                color: $white; } } }
    .trends {
        &-title {
            color: $white; }
        &-descr {
            color: $white; }
        &-cnt {
            background: #242424;
            &__descr {
                color: $white; } }
        &-btn {
            border-color: $white; } }
    .team {
        &-item {
            &__cnt {
                background: #242424; }
            &__descr {
                color: $white; }
            &__social {
                &-link {
                    svg {
                        path {
                            fill: $white; } }
                    &:hover {
                        svg {
                            path {
                                fill: $accent; } } } } } } }
    .philosophy {
        &-descr {
            color: $white; }
        &-info {
            &--gray {
                background: #242424; }
            &__descr {
                color: $white; } } }
    .planing {
        &-item {
            &__title {
                color: $white;
                &:after {
                    background: $white; } }
            &__descr {
                color: $white; } }
        &-step {
            &__title {
                color: $white; }
            &__descr {
                color: $white; } } }
    .contact {
        &-info {
            &__phone-link {
                color: $white;
                &:hover {
                    color: $accent; } }
            &__addr-link {
                color: $white;
                &:hover {
                    color: $accent; } }
            &__title {
                color: $white;
                a {
                    color: $white;
                    &:hover {
                        color: $accent; } } }
            &__social {
                &-link {
                    svg {
                        path {
                            fill: $white; } }
                    &:hover {
                        svg {
                            path {
                                fill: $accent; } } } } } }
        &-form {
            &__title {
                color: $white; } } }
    .error {
        &-info {
            background: #242424; }
        &-descr {
            color: $white; }
        .form-button {
            background: #242424; } }
    .related {
        background: #242424;
        &-title {
            color: $white; } }
    .single {
        &-txt {
            color: $white;
            strong {
                color: $white; } }
        &-title {
            color: $white; }
        &-subtitle {
            color: $white; }
        &-blockquote {
            &__txt {
                color: $white; } }
        &-figcaption {
            color: $white; } }
    .blog {
        &-tag {
            &__item {
                color: $white; }
            &__link {
                color: $white;
                &:hover {
                    color: $accent; } } }
        &-share {
            &__link {
                svg {
                    path {
                        fill: $white; } }
                &:hover {
                    svg {
                        path {
                            fill: $accent; } } } } } }
    .comment {
        &-item {
            &__header {
                color: $white; }
            &__txt {
                color: rgba(255, 255, 255, .75); } } }
    .footer {
        border-color: $white;
        &-logo {
            &-main, &-small {
                color: $white; } }
        .copyright {
            color: $white; }
        &-list {
            &__link {
                color: $white; } }
        &-social {
            &__link {
                svg {
                    path {
                        fill: $white; } } } } } }
