/* 1. Global */

/* 1.1 Libs */
@import "bootstrap-grid.min";
@import "swiper-bundle.min";
@import "magnific-popup";
@import "animsition.min";
@import "aos";

@import "./vars";
@import "./mixins";
/* 1.2 Fonts */
@import "./fonts";
/* 1.3 Sprite */
@import "./sprite_generated";

* {
    box-sizing: border-box; }

html, body {
    overflow-x: hidden; }

body {
    background: #FFF;
    font-family: $sfpt-regular, "Helvetica", Arial, sans-serif;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    margin: 0; }

.wrap {
    height: 100%;
    display: flex;
    flex-direction: column; }


.section {
    padding: 220px 0;
    &--gray {
        background: #F9F9F9; } }

/* 1.4 Typography */

h1, h2, h3, h4, h5, h6, p {
    margin: 0; }

h1 {
    font-family: $sfpd-semibold;
    font-size: 95px;
    line-height: 85px;
    color: $white;
    span {
        color: $accent; } }

ul {
    list-style: none;
    margin: 0;
    padding: 0; }

.title {
    display: inline-block;
    font-family: $sfpd-bold;
    font-size: 23px;
    line-height: 38px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: $black;
    &-line {
        height: 1px;
        background: $black;
        position: absolute;
        bottom: 10px;
        right: 0;
        &-r {
            left: 0;
            width: 200%; } }
    &-wrap {
        display: inline-block;
        position: relative;
        &-r {
            display: block; } } }
/* 1.5 Btns */
.btn {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $accent;
    font-family: $sfpd-semibold;
    font-size: 13px;
    line-height: 18px;
    text-align: center;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: $black;
    padding: 15px;
    max-width: 233px;
    width: 100%;
    text-decoration: none;
    transition: $transition;
    border: 1px solid $accent;
    cursor: pointer;
    &:hover {
        background: transparent;
        color: $accent; } }
/* 1.6 Form */
.form-control {
    height: 50px;
    padding: 16px 18px;
    width: 100%;
    background: $white;
    border: 1px solid $black;
    border-radius: 0;
    box-shadow: none;
    &:focus-visible {
        outline: none; } }
.form-button {
    border: 0;
    outline: 0;
    background: $white;
    cursor: pointer;
    border-radius: 0; }


input[type="submit"] {
    border-radius: 0;
    -webkit-appearance: none; }

.inner-title {
    color: $black;
    max-width: 555px;
    width: 100%;
    font-size: 57px;
    line-height: 60px;
    &-wrap {
        position: relative;
        margin-bottom: 65px;
        &:after {
            content: '';
            height: 1px;
            width: 100%;
            background: rgba(0, 0, 0, 0.3);
            position: absolute;
            left: 0;
            right: 0;
            margin: auto;
            bottom: 10px; } } }

/* 1.7 Filter btns */
.inner {
    &-navbar {
        margin-bottom: 40px; }
    &-nav {
        display: flex;
        align-items: center;
        overflow: auto;
        padding-bottom: 16px;
        &__item {
            margin-right: 42px;
            &:last-child {
                margin-right: 0; } }
        &__link {
            font-size: 15px;
            line-height: 18px;
            color: $black;
            text-decoration: none;
            transition: $transition;
            padding: 0;
            border: 0;
            background: transparent;
            cursor: pointer;
            &:hover {
                color: $accent; }
            &.mixitup-control-active {
                color: $accent; } } }
    &-title {
        &-wrap {
            .container {
                position: relative; } } }
    &-btns {
        position: absolute;
        right: 15px;
        bottom: 0;
        z-index: 1; }
    &-btn {
        font-family: $sfpd-bold;
        font-size: 24px;
        line-height: 38px;
        letter-spacing: 0.03em;
        text-transform: uppercase;
        color: $black;
        text-decoration: none;
        display: flex;
        align-items: center;
        justify-content: center;
        &-arrow {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-left: 10px;
            width: 21px;
            height: 21px;
            margin-bottom: 2px;
            border-radius: 50%;
            background: $accent;
            transition: $transition;
            i {
                margin-left: 2px; } }
        &:hover {
            .inner-btn-arrow {
                transform: translateX(5px); } } } }

/* 1.8 Preloader */
.preloader {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    display: flex;
    justify-content: center;
    width: 100%;
    height: 100%;
    background-color: #ffffff; }

.preloader__spinner {
    position: relative;
    width: 50px;
    height: 50px;
    align-self: center; }


.preloader__double-bounce {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    width: 50px;
    height: 50px;
    animation: bounce 2s infinite ease-in-out;
    opacity: 0.6;
    border-radius: 50%;
    background-color: $accent; }

.preloader__double-bounce--delay {
    animation-delay: -1s; }

@keyframes bounce {
    0%,
    100% {
        transform: scale(0); }
    50% {
        transform: scale(1); } }

[class^=svg-image-] {
    display: inline-block;
    vertical-align: middle; }
